<template>
  <div class="recommend">
    <div class="likes">
      <img class="auto-img" src="../assets/images/cnxh-title.png" alt="" srcset="">
    </div>
    <div class="recommendList clearfix">
      <div
        class="item fl"
        v-for="(item,index) in productlist"
        :key="index"
        @click="goDetail(index)"
      >
        <img v-if="item.goods_small_logo == 0" :src="require('../assets/images/kzt.png')" alt />
        <img v-else v-lazy="item.goods_small_logo" alt />
        <span>{{item.goods_name}}</span>
        <div class="recommend-price-box">
          <div class="money fl" v-if="item.goods_price == 0">
            ￥
            <a>9999</a>
          </div>
          <div class="money fl" v-else>
            ￥
            <a>{{item.goods_price}}</a>
          </div>
          <div class="hot fl">999人已购买</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //商品类型
      typeData: [],
      productlist: [],
      names: "",
    };
  },
  created() {
    // 获取商品数据
    this.Categories();
  },
  methods: {
    // 获取商品数据
    Categories() {
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });
      this.axios({
        methods: "GET",
        url: "/categories",
      })
        .then((result) => {
          this.$toast.clear();
          if ((result.data.meta.status = 200)) {
            //获取商品数据下的商品分类
            result.data.message.forEach((v) => {
              v.children.forEach((val) => {
                if (val.children) {
                  val.children.forEach((value) => {
                    this.typeData.push(value);
                  });
                }
              });
            });

            // Replace
            // 获取一个商品分类
            this.typeData
              .sort(function () {
                return Math.random() - 0.5;
              })
              .splice(0, this.typeData.length - 1);
            this.names = this.typeData[0].cat_name;
            // Replace
            this.Productlist();
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },
    //获取商品分类数据
    Productlist() {
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });
      this.axios({
        methods: "GET",
        url: "/goods/search",
        params: {
          query: this.names,
        },
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.meta.status == 200) {
            this.productlist = result.data.message.goods;
            if (this.productlist == 0 || this.productlist.length < 10) {
              this.Remedy();
              
            } else {
              return
            }
            // Replace
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },
    //如果商品分类数据为空
    Remedy() {
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });
      this.axios({
        methods: "GET",
        url: "/goods/search",
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.meta.status == 200) {
            this.productlist = result.data.message.goods;
          }
        })
        .catch((err) => {
          this.$toast.clear();
          Replace
        });
    },
    //跳转详情页
    goDetail(index) {
      let keywords = this.productlist;
      let goods_id = this.productlist[index].goods_id;
      this.$router.push({ name: "Detail", query: { keywords, goods_id } });
    },
  },
};
</script>

<style lang="less" scoped>
.recommend {
  width: 100%;
  height: 100%;
  .likes{
    text-align: center;
    width: 90%;
    margin: 15px 5% 0 5%;
    padding: 10px 0;
    border-radius: 10px;
    background-image: linear-gradient(to right, #7ae6f7, #3296f8);
    box-shadow: 4px 4px 4px #9e9e9dea;
    img{
      margin: 0 10%;
      width: 80%;
    }
  }
  .recommendList {
    width: 100%;
    height: 100%;
    .item {
      width: 40%;
      border-radius: 5px;
      padding: 2.5%;
      margin: 2.5%;
      height: 100%;
      // border: 1px solid black;
      background-color: white;
      box-shadow: 4px 4px 10px #ccc;
      img {
        width: 100%;
        height: 150px;
      }
      span {
        font-size: 15px;
        font-weight: 600;
        height: 40px;

        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
      }
      .recommend-price-box {
        margin-top: 3px;
        height: 23px;
        .money {
          color: #eb5211;
          font-size: 13px;
          font-weight: 600;
          a {
            font-size: .45rem;
          }
        }
        .hot {
          margin: 2px 0px 0px 5px;
          font-size: .34rem;
          color: #999999;
        }
      }
    }
    .item:last-child {
      margin-bottom: 60px;
    }
  }
}
</style>