<template>
  <div class="Spike">
    <div class="title_wrap">
      <div class="seckill clearfix">
        <div class="seckill-tit fl">天天秒杀</div>
        <div class="seckill-timer-wrap fl">
          <div class="seckill-nth fl">今天场</div>
          <div class="seckill_time fl">{{countDownList}}</div>
        </div>
      </div>
      <div class="seckill-more-icon fr clearfix">
        <img
          class="auto-img"
          src="../assets/images/ttms_r.png"
          alt
        />
      </div>
    </div>
    <div class="recommendList clearfix">
      <div
        class="item fl"
        v-for="(item,index) in productlist"
        :key="index"
        @click="goDetail(index)"
      >
        <img
          class="auto-img"
          v-if="item.goods_small_logo == 0"
          :src="require('../assets/images/kzt.png')"
          alt
        />
        <img class="auto-img" v-else v-lazy="item.goods_small_logo" alt />
        <div class="recommend-price-box clearfix">
          <div class="money fl" v-if="item.goods_price == 0">
            ￥
            <a>9999</a>
          </div>
          <div class="money fl" v-else>
            ￥
            <a>{{item.goods_price}}</a>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      countDownList: "00:00:00",
      //商品类型
      typeData: [],
      productlist: [],
      names: "",
    };
  },
  created() {
    this.countDown();
    // 获取商品数据
    this.Categories();
  },

  methods: {
    timeFormat(param) {
      return param < 10 ? "0" + param : param;
    },
    countDown(it) {
      var interval = setInterval(() => {
        let tomorrow = new Date();
        tomorrow.setTime(tomorrow.getTime() + 24 * 60 * 60 * 1000);
        let Tomo =
          tomorrow.getFullYear() +
          "-" +
          (tomorrow.getMonth() + 1) +
          "-" +
          tomorrow.getDate();
        // 获取当前时间
        let newTime = new Date().getTime();
        //获取明天时间
        let endTime = new Date(Tomo).getTime();
        let obj = null; // 如果活动未结束，对时间进行处理
        if (endTime - newTime > 0) {
          let time = (endTime - newTime) / 1000; // 获取天、时、分、秒
          let day = parseInt(time / (60 * 60 * 24));
          let hou = parseInt((time % (60 * 60 * 24)) / 3600);
          let min = parseInt(((time % (60 * 60 * 24)) % 3600) / 60);
          let sec = parseInt(((time % (60 * 60 * 24)) % 3600) % 60);
          obj = {
            day: this.timeFormat(day),
            hou: this.timeFormat(hou),
            min: this.timeFormat(min),
            sec: this.timeFormat(sec),
          };
        } else {
          // 活动已结束，全部设置为'00'
          obj = {
            day: "00",
            hou: "00",
            min: "00",
            sec: "00",
          };
          clearInterval(interval);
        }
        this.countDownList = obj.hou + ":" + obj.min + ":" + obj.sec;
      }, 1000);
    },
    // 获取商品数据
    Categories() {
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });
      this.axios({
        methods: "GET",
        url: "/categories",
      })
        .then((result) => {
          this.$toast.clear();
          if ((result.data.meta.status = 200)) {
            //获取商品数据下的商品分类
            result.data.message.forEach((v) => {
              v.children.forEach((val) => {
                if (val.children) {
                  val.children.forEach((value) => {
                    this.typeData.push(value);
                  });
                }
              });
            });

            // Replace
            // 获取一个商品分类
            this.typeData
              .sort(function () {
                return Math.random() - 0.5;
              })
              .splice(0, this.typeData.length - 1);
            this.names = this.typeData[0].cat_name;
            // Replace
            this.Productlist();
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },
    //获取商品分类数据
    Productlist() {
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });
      this.axios({
        methods: "GET",
        url: "/goods/search",
        params: {
          query: this.names,
        },
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.meta.status == 200) {
            this.productlist = result.data.message.goods;
            if (this.productlist == 0 || this.productlist.length < 10) {
              this.Remedy();
              
            } else {
              return;
            }
            // Replace
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },
    //如果商品分类数据为空
    Remedy() {
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });
      this.axios({
        methods: "GET",
        url: "/goods/search",
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.meta.status == 200) {
            this.productlist = result.data.message.goods;
          }
        })
        .catch((err) => {
          this.$toast.clear();
          Replace
        });
    },
    //跳转详情页
    goDetail(index) {
      let keywords = this.productlist;
      let goods_id = this.productlist[index].goods_id;
      this.$router.push({ name: "Detail", query: { keywords, goods_id } });
    },
  },
};
</script>

<style lang="less" scoped>
.Spike {
  width: 95%;
  height: 100%;
  margin-left: 2.5%;
  margin-top: 3%;
  box-shadow: 4px 4px 4px #ddd;
  background-color: #fff;
  .title_wrap {
    position: relative;
    width: 100%;
    background: url('../assets/images/ttms.png');
    background-size: contain;
    .seckill {
      width: 52%;
      height: 50px;
      margin-left: 12px;
      line-height: 20px;
      .seckill-tit {
        display: inline-block;
        font-family: PingFangSC-Semibold;
        width: 36%;
        font-size: 0.4rem;
        margin-top: 10px;
        font-weight: 600;
      }
      .seckill-timer-wrap {
        display: inline-block;
        width: 62%;
        height: 20px;
        margin-top: 9px;
        border-radius: 15px;
        border: 1px solid #f2270c;
        border-left: 0;
        .seckill-nth {
          width: 1.2rem;
          text-align: center;
          height: 20px;
          font-size: 12px;
          color: white;
          border-radius: 15px;
          background-image: linear-gradient(90deg, #f10001, #fe4e18);
        }
        .seckill_time {
          font-family: PingFangSC-Regular;
          margin-left: 0.1rem;
          font-size: 0.35rem;
          font-weight: 600;
          color: #f2270c;
        }
      }
    }
    .seckill-more-icon {
      position: absolute;
      width: 16px;
      height: 16px;
      right: 0.3rem;
      top: 13px;
    }
  }
  .recommendList {
    display: -webkit-flex;
    white-space: nowrap;
    overflow-x: auto;
    .item {
      width: 100px;
      height: 35px;
      border-radius: 5px;
      padding: 2.5%;
      background-color: white;
      img {
        width: 65px;
        height: 65px;
      }
      .recommend-price-box {
        margin-top: 9px;
        height: 23px;
        .money {
          color: #eb5211;
          font-weight: 600;
          a {
            font-size: 0.45rem;
          }
        }
      }
    }
    .item:last-child {
      margin-bottom: 60px;
    }
  }
}
</style>